import React from 'react';
import Hero75 from "../images/75per.jpeg";
import Hero100 from "../images/100per.jpeg";
import Hero50 from "../images/50per.jpeg";

const Slider = () => {
    return (
        <div id="carouselExampleCaptions" className="carousel slide" data-bs-ride="carousel" data-bs-interval="2000">
            <div className="carousel-inner">
                <div className="carousel-item active">
                    <img src={Hero75} className="d-block w-100" alt="Hero1" />
                    <div className="carousel-caption d-flex align-items-center justify-content-start text-start" style={{ position: 'absolute', top: 0, left: '10%', width: '90%', height: '100%' }}>
                        {/* Adjusted left padding */}
                        <div>
                            <p className="fw-bold" style={{ fontSize: '35px', fontFamily: 'Arial, sans-serif' }}>The right light can turn a house<br />
                                into your own home.</p>
                            <button type="button" className="btn btn-primary mt-4">
                                Explore
                            </button>
                        </div>
                        <div className="d-flex align-items-end" style={{ position: 'absolute', bottom: '10%', right: '10%' }}>
                            <span className="material-symbols-outlined">living</span>
                            <span className="material-symbols-outlined">living</span>
                            <span className="material-symbols-outlined">living</span>
                            <span className="material-symbols-outlined">living</span>
                        </div>
                    </div>
                </div>
                {/* Similar modifications for other carousel items */}
                <div className="carousel-item">
                    <img src={Hero50} className="d-block w-100" alt="Hero2" />
                    <div className="carousel-caption d-flex align-items-center justify-content-start text-start" style={{ position: 'absolute', top: 0, left: '10%', width: '90%', height: '100%' }}>
                        {/* Adjusted left padding */}
                        <div>
                            <p className="fw-bold" style={{ fontSize: '35px', fontFamily: 'Arial, sans-serif' }}>The right light can turn a house<br />
                                into your own home.</p>
                            <button type="button" className="btn btn-primary mt-4">
                                Explore
                            </button>
                        </div>
                        <div className="d-flex align-items-end" style={{ position: 'absolute', bottom: '10%', right: '10%' }}>
                            <span className="material-symbols-outlined">living</span>
                            <span className="material-symbols-outlined">living</span>
                            <span className="material-symbols-outlined">living</span>
                            <span className="material-symbols-outlined">living</span>
                        </div>
                    </div>
                </div>
                <div className="carousel-item">
                    <img src={Hero100} className="d-block w-100" alt="Hero3" />
                    <div className="carousel-caption d-flex align-items-center justify-content-start text-start" style={{ position: 'absolute', top: 0, left: '10%', width: '90%', height: '100%' }}>
                        {/* Adjusted left padding */}
                        <div>
                            <p className="fw-bold" style={{ fontSize: '35px', fontFamily: 'Arial, sans-serif' }}>The right light can turn a house<br />
                                into your own home.</p>
                            <button type="button" className="btn btn-primary mt-4">
                                Explore
                            </button>
                        </div>
                        <div className="d-flex align-items-end" style={{ position: 'absolute', bottom: '10%', right: '10%' }}>
                            <span className="material-symbols-outlined">living</span>
                            <span className="material-symbols-outlined">living</span>
                            <span className="material-symbols-outlined">living</span>
                            <span className="material-symbols-outlined">living</span>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Slider;
